@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.movies .movie {
  background-color: white;
  width: 45%;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  color: #adaeb9;
  border-radius: 5px;
  transition: box-shadow 0.3s;
}
.movies .movie:hover {
  cursor: default;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movies .movie .link {
  display: flex;
  text-decoration: none;
  color: inherit;
}

.movie_poster {
  position: relative;
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie_title,
.movie_year {
  margin: 0;
  font-weight: 300;
}

.movie_title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie_genres_list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.movie_genre {
  margin-right: 10px;
  font-size: 14px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  background-color: #eeeeee;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.gravity_center,
.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.text_center {
  text-align: center;
}

.gravity_center {
  font-weight: normal;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.about_main {
  background-color: white;
  padding: 3em;
  max-width: 50em;
  border-radius: 0.5em;
  transition: box-shadow 0.3s;
}
.about_main:hover {
  cursor: default;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.navbar {
  position: fixed;
  top: 1em;
  left: 1em;
  display: flex;
  flex-direction: column;
  background-color: white;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 0.25em;
  transition: box-shadow 0.3s;
}
.navbar:hover {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.navbar .link:link {
  color: blue;
  display: inline-block;
  font-weight: bold;
  padding: 1em;
  text-decoration: none;
}
.navbar .link:hover {
  background-color: #e6e6e6;
}
.navbar .link:visited {
  color: blue;
  text-decoration: none;
}

.movie_details_main {
  background-color: white;
  padding: 3em;
  max-width: 60em;
  border-radius: 0.5em;
  transition: box-shadow 0.3s;
}

.movie_details_main img {
  transition: box-shadow 0.3s;
}

.movie_details_main img:hover,
.movie_details_main:hover {
  cursor: default;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie_details_main .head_label {
  display: inline-block;
  text-align: center;
  width: 6.5em;
  margin-right: 0.5em;
  padding: 0.2em 0.5em;
  background-color: #929292;
  color: white;
  border-radius: 1em;
}

